<template>
  <v-container>
    <v-row class="text-center bg-primary-gradient">
      <v-col cols="12">
        <div class="mb-5" style="position: relative">
          <v-avatar size="120">
            <v-img
                :src="avatar.url"
                contain
            />
          </v-avatar>
          <div style="position: absolute; bottom: 0; left: calc(50% + 30px);">
            <v-file-input
                @change="previewAvatar"
                v-model="avatar.image"
                prepend-icon="mdi-camera"
                hide-input
            >
            </v-file-input>
          </div>
        </div>

        <div class="text-center mb-3" style="color: white; font-size: 1.5rem; font-weight: bold">Perfil de usuario</div>

        <v-select
            v-model="form.identity_document_id"
            :items="items.documentTypes"
            item-text="name"
            item-value="id"
            label="Tipo de documento"
            filled
            background-color="white"
        ></v-select>

        <v-text-field
            v-model="form.identity_document_number"
            label="Numero de documento"
            filled
            background-color="white"
        ></v-text-field>

        <v-select
            v-model="form.ubigeo_department_id"
            :items="items.ubigeoDepartments"
            item-text="name"
            item-value="id"
            label="Departamento"
            filled
            background-color="white"
        ></v-select>

        <v-text-field
            v-model="form.name"
            label="Nombres"
            filled
            background-color="white"
        ></v-text-field>

        <v-text-field
            v-model="form.last_name"
            label="Apellidos"
            filled
            background-color="white"
        ></v-text-field>

        <v-text-field
            v-model="form.email"
            label="Correo"
            filled
            background-color="white"
        ></v-text-field>

        <v-btn :loading="isLoading" @click="onSave" class="mb-3" block>Guardar perfil</v-btn>

        <v-btn @click="onBack" class="mb-5" block>Volver al inicio</v-btn>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import {mapGetters} from "vuex";
import axios from 'axios'

export default {
  name: 'ProfileIndex',
  inject: ['notyf'],
  components: {},
  data: () => ({
    isLoading: false,
    avatar: {
      image: '',
      url: '',
    },
    form: {
      identity_document_id: '',
      identity_document_number: '',
      ubigeo_department_id: '',
      name: '',
      last_name: '',
      email: '',
    },
    items: {
      documentTypes: [],
      ubigeoDepartments: [],
    }
  }),
  computed: {
    ...mapGetters(['user']),
  },
  mounted() {
    this.loadDocuments()
    this.loadDepartments()

    this.avatar.url = this.user.photo
    this.form.identity_document_id = this.user.identity_document_id
    this.form.identity_document_number = this.user.identity_document_number
    this.form.name = this.user.name
    this.form.last_name = this.user.last_name
    this.form.email = this.user.email
    this.form.ubigeo_department_id = parseInt(this.user.ubigeo_department_id)
  },
  methods: {
    loadDocuments() {
      axios.get('identity-documents').then((response) => {
        this.items.documentTypes = response.data
      })
    },
    loadDepartments() {
      axios.get('ubigeo/departments').then((response) => {
        this.items.ubigeoDepartments = response.data
      })
    },
    onSave() {
      this.isLoading = true
      let formData = new FormData()
      formData.append('photo', this.avatar.image)
      formData.append('identity_document_id', this.form.identity_document_id)
      formData.append('identity_document_number', this.form.identity_document_number)
      formData.append('name', this.form.name)
      formData.append('last_name', this.form.last_name)
      formData.append('email', this.form.email)
      formData.append('ubigeo_department_id', this.form.ubigeo_department_id)

      axios.post('/profile', formData, {headers: {'Content-Type': 'multipart/form-data'}}).then((response) => {
        this.$store.dispatch('login', response.data)
        this.notyf.success('Perfil actualizado correctamente.');
      }).catch((error) => {
        this.notyf.error(error.response.data.message);
      }).then(() => {
        this.isLoading = false
      })
    },
    onBack() {
      this.$router.push({name: 'Home'})
    },
    previewAvatar() {
      //this.avatar.image = file
      this.avatar.url = URL.createObjectURL(this.avatar.image)
    }
  }
}
</script>
